import vaultInterface from '@/hooks/vault-interface'
import useGetApiVaultInfo from '@/hooks/vaults/useGetApiVaultInfo'
import { useCalculateChangeLpRate, useCalculateSlippageAmount } from '@/hooks/vaults/useVaultsHelper'
import useUnstableVaultsListStore from '@/store/unstable-vaults/useVaultsList'
import { VaultApiInfo } from '@/types'
import { TradeInputGroup } from '@cetus/design'
import { useAccountBalance } from '@cetus/hooks'
import { useAccountStore } from '@cetus/stores'
import { Token } from '@cetus/types'
import { HTextLabelBox } from '@cetus/ui-kit'
import { d, formatCurrency, formatNumber, fromDecimalsAmountFix } from '@cetus/utils'
import { Box, Button, Text, VStack } from '@chakra-ui/react'
import { useEffect, useMemo } from 'react'
type VaultWithdrawModeProps = {
  isUnstableVault: boolean
  displayTokenA: Token
  displayTokenB: Token
  vaultId: string
  isReverse: boolean
  assetAction: string
  isCheckedZAP: boolean
  currentActionTab: string
  apiVaultInfo: VaultApiInfo
  autoRefresh: () => void
}
export default function VaultWithdrawMode(props: VaultWithdrawModeProps) {
  const { isUnstableVault, displayTokenA, displayTokenB, vaultId, isReverse, assetAction, isCheckedZAP, currentActionTab, apiVaultInfo } = props
  const useVaultsRemove = vaultInterface.useVaultsRemove(isUnstableVault)
  const useAddButtonStatus = vaultInterface.useAddButtonStatus(isUnstableVault)
  const useGetContractVaultInfo = vaultInterface.useGetContractVaultInfo(isUnstableVault)
  // const { apiVaultInfo, tokenA, tokenB, isReverse, vaultsId, totalLp, holdCoinAmounts, holdLpAmount, handleRefreshClick } = props
  const { getApiVaultInfo } = useGetApiVaultInfo(vaultId)
  const { currentAccount, isOpenWalletModal, onWalletModal } = useAccountStore()
  const { fetchAccountBalance } = useAccountBalance()

  const {
    contractVault,
    lpTokenInfo,
    balanceInfo,
    fetchClmmPoolFromContract,
    fetchVaultInfoFromContract,
    holdCoinAmounts,
    fromTokenLock,
    toTokenLock,
    fetchVaultBalance,
    isBalanceLoading
  } = useGetContractVaultInfo(
    apiVaultInfo?.vaultsId,
    apiVaultInfo?.clmmPoolAddress,
    apiVaultInfo?.tokenA,
    apiVaultInfo?.tokenB,
    apiVaultInfo?.isReverse
  )

  const autoRefresh = () => {
    // 刷新余额
    fetchAccountBalance()

    console.log('🚀🚀🚀 ~ index.tsx:57 ~ autoRefresh ~ apiVaultInfo:', apiVaultInfo)
    if (apiVaultInfo?.category == 'haedal') {
      fetchVaultBalance(currentAccount?.address, vaultId, apiVaultInfo?.tokenA?.decimals)
    }

    // 刷新 clmm 合约
    if (apiVaultInfo) {
      fetchClmmPoolFromContract(apiVaultInfo.clmmPoolAddress, true)
    }

    // 刷新Vaults 合约
    if (vaultId) {
      fetchVaultInfoFromContract(vaultId, true)
      getApiVaultInfo(vaultId, true)
    }
    console.log('🚀🚀🚀 ~ index.tsx:68 ~ autoRefresh ~ autoRefresh:', autoRefresh)
  }
  console.log('🚀🚀🚀 ~ WithdrawMode.tsx:69 ~ VaultWithdrawMode ~ holdCoinAmounts:', holdCoinAmounts)

  const { contractClmmPoolObj } = useUnstableVaultsListStore()

  const contractClmmPoolInfo: any = useMemo(() => {
    if (contractClmmPoolObj && contractClmmPoolObj[apiVaultInfo?.clmmPoolAddress]) {
      return contractClmmPoolObj[apiVaultInfo?.clmmPoolAddress]
    }
    return undefined
  }, [apiVaultInfo?.clmmPoolAddress, contractClmmPoolObj])
  const {
    amountInputA,
    amountInputB,
    availableAmountA,
    availableAmountB,
    amountValueA,
    amountValueB,
    preCalculateLoading,
    fixAmountA,
    inputTotalValue,
    handleAmountInputChange,
    calculateResult,
    showOnlySideTips,
    doRemoveAction,
    removePercent,
    isPercentInput,
    reCalculateResult,
    calculateAvailableLoading
  } = useVaultsRemove(
    assetAction,
    autoRefresh,
    holdCoinAmounts,
    contractVault?.lp_token_treasury,
    balanceInfo?.balance,
    vaultId,
    isReverse ? displayTokenB : displayTokenA,
    isReverse ? displayTokenA : displayTokenB,
    isReverse,
    fromTokenLock,
    toTokenLock,
    isCheckedZAP,
    contractClmmPoolObj
  )

  const inputTotalValueError = useMemo(() => {
    console.log('🚀🚀🚀 ~ VaultsRemove.tsx:178 ~ inputTotalValueError ~ inputTotalValue:', inputTotalValue)
    if (inputTotalValue && isCheckedZAP) {
      return d(inputTotalValue).gt(0) && d(inputTotalValue).lt(0.0001)
    }
    return false
  }, [isCheckedZAP, inputTotalValue])

  //totalLp,holdLpAmount
  const { btnText, btnDisabled } = useAddButtonStatus(
    isCheckedZAP,
    assetAction,
    false,
    amountInputA,
    amountInputB,
    isReverse ? !fixAmountA : fixAmountA,
    availableAmountA,
    availableAmountB,
    displayTokenA,
    displayTokenB,
    isUnstableVault,
    '',
    calculateResult,
    inputTotalValueError
  )
  // 占比
  const { lpRate } = useCalculateChangeLpRate(
    calculateResult?.burn_ft_amount,
    isUnstableVault ? contractVault?.lp_token_treasury : contractVault?.total_supply
  )
  // 最小可接收
  const { amountLimit } = useCalculateSlippageAmount(0, calculateResult?.burn_ft_amount, false)

  const balanceA = useMemo(() => {
    return isReverse ? availableAmountB : availableAmountA
  }, [availableAmountB, availableAmountA, isReverse])

  const balanceB = useMemo(() => {
    return isReverse ? availableAmountA : availableAmountB
  }, [availableAmountA, availableAmountB, isReverse])

  const showTokenALoading = useMemo(() => {
    if (preCalculateLoading) {
      if (isPercentInput) {
        return true
      }
      if (isReverse) {
        return fixAmountA
      }
      return !fixAmountA
    }
    return false
  }, [preCalculateLoading, isPercentInput])

  const showTokenBLoading = useMemo(() => {
    if (preCalculateLoading) {
      if (isPercentInput) {
        return true
      }
      if (isReverse) {
        return !fixAmountA
      }
      return fixAmountA
    }
    return false
  }, [preCalculateLoading, isPercentInput])

  useEffect(() => {
    reCalculateResult()
  }, [apiVaultInfo?.vaultsId])

  useEffect(() => {
    if (apiVaultInfo && apiVaultInfo?.clmmPoolAddress) {
      console.log('🚀🚀🚀 ~ withdraw-mode.tsx:170 ~ useEffect ~ apiVaultInfo:', apiVaultInfo)
      fetchClmmPoolFromContract(apiVaultInfo.clmmPoolAddress, true)
    }
  }, [apiVaultInfo?.clmmPoolAddress])

  useEffect(() => {
    if (vaultId) {
      fetchVaultInfoFromContract(vaultId)
    }
  }, [vaultId])

  // useEffect(() => {
  //   getApiVaultInfo(vaultId, false)
  // }, [])

  useEffect(() => {
    console.log('🚀🚀🚀 ~ withdraw-mode.tsx:188 ~ useEffect ~ lpTokenInfo:', lpTokenInfo)
    console.log('🚀🚀🚀 ~ withdraw-mode.tsx:188 ~ useEffect ~ apiVaultInfo:', apiVaultInfo)
    if (currentAccount?.address && apiVaultInfo?.vaultsId && lpTokenInfo?.decimals && apiVaultInfo?.isUnstableVault) {
      console.log('🚀🚀🚀 ~ withdraw-mode.tsx:189 ~ useEffect ~ fetchVaultBalance:', fetchVaultBalance)
      fetchVaultBalance(currentAccount?.address, apiVaultInfo.vaultsId, lpTokenInfo?.decimals)
    }
  }, [currentAccount?.address, apiVaultInfo?.vaultsId, lpTokenInfo?.decimals])

  useEffect(() => {
    reCalculateResult()
  }, [availableAmountA, availableAmountB])

  const LPBurnAmount = useMemo(() => {
    return formatNumber(fromDecimalsAmountFix(amountLimit || '0', lpTokenInfo?.decimals), lpTokenInfo?.decimals)
  }, [amountLimit, lpTokenInfo])

  return (
    <>
      <Box w="100%" position="relative">
        <TradeInputGroup
          onClick={() => {}}
          from={{
            wrapStyle: { h: '108px' },
            balance: availableAmountA || '',
            value: amountInputA,
            amountValue: amountValueA,
            loading: false,
            calculateAvailableLoading: isBalanceLoading || calculateAvailableLoading,
            hideSelf: assetAction !== 'both' && assetAction !== displayTokenA?.coin_type,
            onChange: (value: string, isClickMax?: boolean, isHalfClickMax?: boolean) => {
              console.log('🚀🚀🚀 ~ withdraw-mode.tsx:194 ~ VaultWithdrawMode ~ value:', value)
              handleAmountInputChange(value, !isReverse, isClickMax, isHalfClickMax)
            },
            selectable: false,
            placeholder: '0.0',
            token: displayTokenA,
            balanceLabel: 'Available',
            onFocusChange: (focus: boolean) => {}
          }}
          to={{
            wrapStyle: { h: '108px' },
            balance: availableAmountB || '',
            value: amountInputB,
            amountValue: amountValueB,
            loading: false,
            calculateAvailableLoading: isBalanceLoading || calculateAvailableLoading,
            onFocusChange: (focus: boolean) => {},
            onChange: (value: string, isClickMax?: boolean, isHalfClickMax?: boolean) => {
              handleAmountInputChange(value, isReverse, isClickMax, isHalfClickMax)
            },
            selectable: false,
            hideSelf: assetAction !== 'both' && assetAction !== displayTokenB?.coin_type,
            placeholder: '0.0',
            token: displayTokenB,
            balanceLabel: 'Available'
          }}
          iconParams={{
            xlinkHref: '#icon-icon_add',
            svgFill: 'text_caption',
            iconCursor: 'default'
          }}
          iconHover={false}
        />
        {/* {[
          fromTokenLock && (assetAction == displayTokenA?.coin_type || assetAction == 'both'),
          toTokenLock && (assetAction == displayTokenB?.coin_type || assetAction == 'both')
        ].filter(Boolean)?.length === 1 && (
          <LockInput
            position="absolute"
            top={assetAction == 'both' ? (fromTokenLock ? '0px' : '116px') : '0px'}
            h="108px"
            borderRadius="16px"
            left="0"
            text=""
          />
        )} */}
      </Box>
      <Button
        width="100%"
        height="52px"
        borderRadius="12px"
        marginBottom="8px"
        fontWeight="500"
        isLoading={preCalculateLoading}
        disabled={btnDisabled || preCalculateLoading}
        onClick={() => (!currentAccount?.address ? onWalletModal(true) : doRemoveAction())}
      >
        {/* {btnText || currentActionTab} */}
        {btnText || (isCheckedZAP ? 'Zap Out' : 'Withdraw')}
      </Button>
      {/* <HStack marginBottom="12px" width="100%">
        <VaultsAddResult
          amountLimit={amountLimit}
          inputTotalValue={inputTotalValue}
          preCalculateLoading={preCalculateLoading}
          lpRate={lpRate}
          lpDecimals={lpTokenInfo?.decimals}
          poolName={`${displayTokenA?.symbol} - ${displayTokenB?.symbol}`}
          labelColor="primary_gray"
        />
      </HStack> */}
      {inputTotalValueError && (
        <VStack w="100%" alignItems="start" mb="8px">
          <Text
            color="primary_red"
            fontSize="12px"
            textAlign="left"
            w="100%"
            bg="primary_red_opacity.10"
            p="12px"
            borderRadius="8px"
            lineHeight="20px"
          >
            The input is too small. Zap mode is not available.
          </Text>
        </VStack>
      )}
      {/* 展示计算结果 */}
      {(calculateResult || showOnlySideTips) && !inputTotalValueError && (
        <VStack w="100%" gap="20px" pb="20px">
          {assetAction !== 'both' && (
            <Text color="text_paragraph" fontSize="12px" lineHeight="1.8">
              {showOnlySideTips}
            </Text>
          )}
          <VStack w="100%" gap="20px">
            <HTextLabelBox
              label="Total Withdraw"
              labelStyle={{
                fontSize: '14px'
              }}
              value={formatCurrency(inputTotalValue, 2)}
              valueStyle={{
                fontSize: '14px'
              }}
              isLoading={preCalculateLoading}
            />
            <HTextLabelBox
              label="LP Burn Amount"
              value={`${LPBurnAmount} ${`${displayTokenA?.symbol} - ${displayTokenB?.symbol}`}`}
              labelStyle={{
                fontSize: '14px'
              }}
              valueStyle={{
                fontSize: '14px'
              }}
              isLoading={preCalculateLoading}
            />
          </VStack>
        </VStack>
      )}
    </>
  )
}
